.map {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 302;
  opacity: 0;
  transition: opacity 1s ease;
  pointer-events: none;

  img {
    pointer-events: none;
  }

  &.show {
    opacity: 1;
    pointer-events: all;
  }

  &__container {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__background {
    width: 100%;
    height: 100%;
  }

  &__item {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;

    &.passed {
      filter: grayscale(1);
      cursor: not-allowed;

      &:hover {
        img {
          filter: drop-shadow(0px 0px 0px #fff);
        }
      }
    }

    .house {
      position: relative;
      flex: 1 1 auto;
      width: 100%;
    }

    .house img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: filter .2s linear;
    }

    &:hover {
      img {
        filter: drop-shadow(0px 0px 7px #fff);
      }
    }
  }

  .milk-house {
    height: 27%;
    width: 21%;
    left: 22%;
    top: 10%;

    .milk-sign {
      width: 16%;
      height: 32%;
      left: 68%;
      top: 19%;
    }
  }

  .home-house {
    height: 21%;
    width: 13%;
    left: 66%;
    top: 2%;

    .map__title {
      margin-bottom: -.5em;
    }
  }

  .butchery-house {
    height: 24%;
    width: 23%;
    left: 24%;
    top: 45%;

    .butchery-sign {
      width: 24%;
      height: 39%;
      left: 32%;
      top: -11%;

    }
  }

  .bakery-house {
    height: 24%;
    width: 13%;
    left: 70.5%;
    top: 50%;

    .bakery-sign {
      width: 26%;
      height: 40%;
      left: 20%;
      top: 13%;
    }
  }

  .fish-house {
    height: 24%;
    width: 34%;
    left: 36%;
    top: 73%;

    .fish-sign {
      width: 16%;
      height: 40%;
      left: 44%;
      top: 79%;
    }

    .map__title {
      margin-top: 1em;
    }
  }

  &__title {
    span {
      font-family: "Cactus Sandwich Fill FM";
      font-size: 2em;
      position: relative;
      color: #EBB237;
      z-index: 1;
    }

    span::before,
    span::after {
      content: attr(data-text);
      position: absolute;
      top: 0;
      left: 0;
    }

    span::before {
      -webkit-text-stroke: .15em black;
    }

    span::after {
      z-index: 0;
      color: #EBB237;

    }
  }


}