button.map-btn {
  position: absolute;
  z-index: 201;
  @include adaptive_value("font-size", 42, 18);
  letter-spacing: .1em;
  color: #fff;
  top:2%;
  left: 1%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 1s ease-in;
}
button.map-btn.show{
  opacity: 1;
  pointer-events: all;
}