#rollingText {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 601;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #c22be1;
  font-size: 4em;
  top: 0;
  letter-spacing: .1em;
  pointer-events: none;
  opacity: 0;
  transition: opacity .4s ease-in;

  p {
    font-family: $Muller;
  }

  &.show {
    opacity: 1;
  }
}