// main scss
* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  user-select: none;
}

html,
body {
  background-color: #000;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  width: 100vw;
  height: 100vh;
}

.preloadImg {
  width: 0;
  height: 0;
}

.img {
  position: relative;
  width: 100%;
  height: 100%;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
    object-position: center;
  }
}

main {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

button {
  margin: 0;
  padding: 0;
  border: none;
}

#motel5 {
  transform: translateX(-101%);
  border-right: 5px solid #000;
  animation: anim1 1.5s linear .5s forwards;
}

#motel52 {
  transform: translateX(101%);
  border-left: 5px solid #000;
  animation: anim2 1.5s linear .5s forwards;
}

@keyframes anim1 {
  0% {
    transform: translateX(-101%);
  }
  100% {
    transform: translateX(-50%);
  }
}

@keyframes anim2 {
  0% {
    transform: translateX(101%);
  }
  100% {
    transform: translateX(50%);
  }
}