.scene1-textBlock, .scene2-textBlock {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  padding: 2em;
  width: 70%;
  //height: 60%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 1em;

  p {
    font-family: Muller, sans-serif;
    font-size: 2em;
    color: #454546;

    span {
      font-family: Roboto, sans-serif;
    }

  }
}
.scene1-textBlock{
  opacity: 0;
}
.scene1-textBlock.show {
  animation: appearance 1s linear 3s forwards;
}

.scene2-textBlock.show {
  animation: unappearance 1s linear 0s forwards;

}

@keyframes appearance {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes unappearance {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}