@use "sass:math";

.talk {
  position: absolute;
  z-index: 500;
  width: 25%;
  background: #fff;
  border-radius: 1em;
  opacity: 0;
  pointer-events: none;

  &.talking {
    pointer-events: all;
    transition: opacity 0.4s ease;
    opacity: 1;
  }

  &__box {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 0.75em;
  }

  &__text {
    p {
      margin: 0;
    }
  }

  &__arrow {
    position: absolute;
    width: 1em;
    height: 1em;
    background: #fff;

    clip-path: polygon(50% 100%, 0 0, 100% 0);
  }

  &.none &__arrow {
    display: none;
  }

  &.bl &__arrow {
    top: 99%;
    left: 5%;
  }

  &.bl45 &__arrow {
    bottom: 1%;
    left: -6%;
    rotate: 45deg;
    translate: 50% 50%;
  }

  &.br &__arrow {
    top: 99%;
    right: 5%;
  }

  &.br45 &__arrow {
    bottom: 0;
    left: 93%;
    rotate: -45deg;
    translate: 50% 50%;
  }

  &.bc &__arrow {
    top: 98%;
    left: 50%;
    transform: translate(-50%, 0%);
  }

  &.cl &__arrow {
    top: 50%;
    left: 0%;
    transform: translate(-100%, -50%) rotate(90deg);
  }

  &.cr &__arrow {
    top: 50%;
    left: 100%;
    transform: translate(0, -50%) rotate(-90deg);
  }

  &.tl &__arrow {
    top: 1%;
    left: 3.2%;
    transform: translate(-100%, -50%) rotate(135deg);
  }

  &.tc &__arrow {
    top: 0%;
    left: 50%;
    transform: translate(-50%, -100%) rotate(180deg);
  }

  &.thinkR, &.thinkRC, &.thinkL, &.thinkLC {
    .talk__arrow {
      display: none;
    }
  }

  &.thinkR {
    .talk__box::before,
    .talk__box::after {
      content: "";
      position: absolute;
      background: #fff;
      border-radius: 99999px;
    }

    .talk__box::before {
      width: 10%;
      height: 23%;
      top: 105%;
      left: 11%;
    }

    .talk__box::after {
      width: 7%;
      height: 23%;
      top: 134%;
      left: 6%;
    }
  }

  &.thinkRC {
    .talk__box::before,
    .talk__box::after {
      content: "";
      position: absolute;
      background: #fff;
      border-radius: 99999px;
    }

    .talk__box::before {
      width: 10%;
      height: 23%;
      top: 44%;
      left: -11%;
    }

    .talk__box::after {
      width: 7%;
      height: 23%;
      top: 67%;
      left: -19%;
    }
  }

  &.thinkL {
    .talk__box::before,
    .talk__box::after {
      content: "";
      position: absolute;
      background: #fff;
      border-radius: 99999px;
    }

    .talk__box::before {
      width: 10%;
      height: 23%;
      top: 24%;
      left: 102%;
    }

    .talk__box::after {
      width: 7%;
      height: 23%;
      top: 46%;
      left: 113%;
    }
  }

  &.thinkLC {
    .talk__box::before,
    .talk__box::after {
      content: "";
      position: absolute;
      background: #fff;
      border-radius: 99999px;
    }

    .talk__box::before {
      width: 10%;
      height: 23%;
      top: 105%;
      left: 79%;
    }

    .talk__box::after {
      width: 7%;
      height: 23%;
      top: 134%;
      left: 88%;
    }
  }
}
